#item-list .custom-scrollbar::-webkit-scrollbar{height:10px;width:10px}#item-list .custom-scrollbar::-webkit-scrollbar-track{background:#fff;border-radius:10px;box-shadow:inset 10px 10px 10px 0px #f0f0f0}#item-list .custom-scrollbar::-webkit-scrollbar-thumb{background:linear-gradient(170deg, var(--gradient-blue) 27%, var(--gradient-green) 64%);border-radius:30px}#item-list .card-container{height:300px;background-position:center center;background-repeat:no-repeat;background-size:cover;overflow:hidden;position:relative}#item-list .card-bg{opacity:1;position:absolute;left:0;top:0;width:100%;height:100%}#item-list .card-content{position:relative;background-color:rgba(255,255,255,.7);white-space:pre-wrap;word-break:break-word;-webkit-hyphens:auto;-moz-hyphens:auto;hyphens:auto}#item-list .card-content li{max-width:140px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}#item-list .card-content li:nth-of-type(n+2):before{content:"•";padding:0 5px}.card-link img{transform:scale(1);transition:transform 700ms}.card-link:hover img{transform:scale(1.2);transition:transform 700ms}